/**
 * Utility mixins that don't warrant a file to themselves
 */

@mixin upload-status {
  display: flex;
  justify-content: center;
  align-items: center;

  &::before {
    content: '';
    display: flex;
    justify-content: center;
    align-items: center;
    transition: width 100ms linear, height 100ms linear;
    width: 0;
    height: 0;
  }
}
